<template>
  <div id="app">
    Parent counter : {{ this.$store.state.counter }}<br>
    <button @click="addCounter">+</button>
    <button v-on:click="subCounter">-</button>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods: {
    addCounter() {
      this.$store.state.counter += 1;
    },
    subCounter() {
      this.$store.state.counter -= 1;
    },
  },
};
</script>

<style lang="scss">
@import './assets/scss/main.scss';
</style>



